.Content-text {
  font: normal normal 400 66px/106px Gilroy;
  letter-spacing: 3.16px;
  line-height: normal;
}
.heading-1,
.heading-2,
.heading-3,
.heading-4 {
  font-size: 60px;
}
.heading-1 {
  color: #3c4852;
}
.heading-2 {
  color: #3c4852;
}
.heading-3,
.heading-4 {
  color: #990000;
}
.MainPageback {
}
.circle1,
.circle2 {
  position: absolute;
}
.circle1 {
  width: 300px;
  left: 0px;
  bottom: 0px;
}
.circle2 {
  width: 240px;
  bottom: -150px;
  left: -120px;
}
@media (max-width: 700px) {
  .Content-text {
    text-align: center;
    font: normal normal 400 46px/66px Gilroy;
    /* display: none; */
  }
}
